:root{
    --primarycolor--:#3F51B5;
    --fontRoboto--:'Roboto', sans-serif;
    --secondarycolor--:#f50057;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.link{
    text-decoration: none;
    color: #f50057;
    color: var(--secondarycolor--);
}



:root{
    --primarycolor--:#3F51B5;
    --fontRoboto--:'Roboto', sans-serif;
    --secondarycolor--:#f50057;
}
main{
    height: 80vh;
    display: flex;
    width: 100%;
    /* background: yellow; */
    justify-content: center;


}
.content{
    display: flex;
    flex: 1 1;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* padding:0% 20%; */
    /* background: rgb(10, 7, 153); */


}
.content >.content-Inner{
    width: 60%;
}
.content>.content-Inner h1{
    
    font-family: 'Roboto', sans-serif;
    
    font-family: var(--fontRoboto--);
    margin-bottom: 5vh;
    color: #f50057;
    color: var(--secondarycolor--);

}
.content >.content-Inner>.link>button{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:8px 20px;
    background: transparent;
    outline: none;
    border: 1px solid #f50057;
    border: 1px solid var(--secondarycolor--);
    cursor: pointer;
}
.content >.content-Inner>.link> button>span{
    color: #f50057;
    color: var(--secondarycolor--);
}
.image{
    /* background: rgb(17, 192, 55); */
    flex: 1 1;
    display: flex;
    justify-content:flex-end;
    align-items: center;
    
}
.image img{
    height: 70%;
    width: 70%;
}

@media All and (max-width:600px){
    .content >.content-Inner{
        width: 100%;
        margin-left: 10px;
    }
    .content>.content-Inner h1{
        font-size: 20px;
    }
    .image img{
        height: 80%;
        width: 90%;
    }

}
.footer{
    background-color: rgb(36, 32, 32);
    height: 40vh;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.footer-content1>h1{
    color: #F50057;
}
.footer-content2 >ul{
    list-style: none;
}
.footer-content2 >ul >li{
    color: white;
    margin-bottom:20px ;
}
@media All and (max-width:600)
{
    .footer-content1>h1{
        font-size: 30px;
    }

}
.image_container{
    height: 100vh;
    width: 100%;
}
img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.image_container::after{
    content: "";
    left: 0;
    right: 0;
    background: black;
    z-index:5;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 10;
    opacity: 0.3;
}
.icon{
    position: absolute;
    background-color:#F50057 ;
    bottom: 100px;
    right:100px;
    border-radius: 40%;
    /* cursor: pointer; */
    z-index: 15;
   

}
.icon:hover{
    background-color:rgb(145, 1, 30) ;
    transition: all 0.5s ease;
}
.backward{
    right: 150px;
}
@media All and (max-width:600px){
    .icon{
        right:30px;

    }
    .backward{
        right: 90px;
    }
}
